.login_logo {
  margin-top: 20px;
  margin-bottom: 20px;
  width: 200px;
  margin-right: auto;
  margin-left: auto;
  object-fit: contain;
}
.login{
    height: 100vh;
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.login_container{
  width: 300px;
  height: fit-content;
  display: flex;
  flex-direction: column;
  border-radius: 5px;
  border: 1px solid lightgray;
  padding: 20px;
}
.login_container > h1{
  font-weight: 500;
  margin-bottom: 20px;
}
.login_container > form >h5{
  margin-bottom: 5px;
}
.login_container > form >input{
  margin-bottom: 10px;
  height: 30px;
  background-color: white;
  width: 98%;
}
.login_container > p{
  margin-top: 15px;
  font-size: 12px;
}

.login_button{
  background: #f0c14b;
  border: 2px solid;
  margin-top: 10px;
  border-color: #a88734 #9c7e31 #846a26;
  color: #111;
  height: 30px;
  width: 100px;
}
.signup_button{
  border-radius: 2px;
  width: 100%;
  height: 30px;
  border: 1px solid;
  margin-top: 10px;
  border-color: darkgray;
}